/* === NEW PROJECT CARD STYLE (Inspired by Uploaded Image) === */
.new__project__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
  margin-bottom: 10rem;
}

.new__project__card {
  background: #f9f9f931;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
  transition: transform 0.3s ease;
}

.new__project__card:hover {
  transform: translateY(-4px);
}

.new__project__image {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.new__project__content {
  padding: 1.25rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.new__project__title {
  font-size: 1.2rem;
  font-weight: 600;
  color: #111827;
}

.new__project__description {
  font-size: 0.95rem;
  color: #4b5563;
  line-height: 1.5;
}

.new__project__tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.new__project__tag {
  background: rgba(205, 244, 244, 0.537);
  color: #0f8381;
  padding: 4px 10px;
  border-radius: 9999px;
  font-size: 0.75rem;
  font-weight: 500;
}

.new__project__link {
  align-self: flex-start;
  font-size: 1.25rem;
  color: var(--first-color);
  margin-top: auto;
  transition: color 0.3s;
}

.new__project__link:hover {
  color: #1f8a7a;
}

/* === RESPONSIVE === */
@media screen and (max-width: 768px) {
  .new__project__grid {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 480px) {
  .new__project__content {
    padding: 1rem;
  }

  .new__project__title {
    font-size: 1rem;
  }

  .new__project__description {
    font-size: 0.9rem;
  }
}
