.aside {
  position: fixed;
  left: 0;
  top: 0;
  backdrop-filter: blur(6px);
  border-right: 1px solid rgba(255, 255, 255, 0.05);
  padding: 2.5rem;
  width: 110px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 10;
  transition: 0.3s;
}

.nav__logo {
  font-family: var(--icon-font);
  color: #14b8a6;
  font-size: 25px;
  margin-left: -13px;
}

.nav__list {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
}

.nav__link {
  font-size: 1.5rem;
  color: var(--title-color);
  font-weight: var(--font-bold);
  transition: 0.3s;
}

.nav__link:hover {
  color: var(--second-color);
}

.copyright {
  color: hsl(245, 15%, 65%);
  font-size: var(--small-font-size);
  transform: rotate(-180deg);
  writing-mode: vertical-rl;
}

.nav__toggle {
  position: fixed;
  top: 1rem;
  left: 1rem;
  cursor: pointer;
  height: 40px;
  width: 45px;
  background-color: rgb(255, 251, 229);
  border: 2px solid #dfdfc468;
  border-radius: 1rem;
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 10;
  transition: 0.3s;
}

@media screen and (max-width: 1024px) {
  .aside {
    left: -110px;
  }

  .nav__toggle {
    display: flex;
  }

  .nav__toggle-open {
    left: 140px;
  }

  .show-menu {
    left: 0;
  }
}
