.timeline-container {
  position: relative;
  padding-left: 80px;
}

.timeline-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 171px;
  width: 3px;
  height: 100%;
  background-color: #e7e7e7;
  z-index: 0;
}

.timeline-item {
  display: flex;
  align-items: flex-start;
  position: relative;
  margin-bottom: 60px;
  padding-right: 20px;
}

/* Logo on the far left */
.timeline-left {
  width: 60px;
  margin-right: 50px;
  display: flex;
  align-items: flex-start;
}

.timeline-logo {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  object-fit: cover;
  background-color: white;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Timeline line center with marker */
.timeline-center {
  width: 20px;
  left: -25px;
  position: relative;
}

.timeline-marker {
  position: absolute;
  top: 16px;
  width: 15px;
  height: 15px;
  background-color: #86d4ca;
  border-radius: 50%;
  border: 3px solid white;
  z-index: 1;
}

/* Card to the right */
.timeline-right {
  flex: 1;
}

.timeline-card {
  background: #ffffff54;
  padding: 20px 24px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
}

.timeline-card:hover {
  background: #ffffff86;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.12);
  transform: translateY(-4px);
}

/* Add triangle pointer */
.timeline-card::before {
  content: "";
  position: absolute;
  top: 20px; /* aligns vertically with marker */
  left: 120px; /* sticks out to the left */
  width: 0;
  height: 0;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-right: 10px solid #ffffff54;
  background: #ffffff54;
  transition: none; /* prevents unintended animation */
  pointer-events: none; /* avoids interaction */

  z-index: -1;
}
.timeline-card:hover::before {
  border-right-color: #ffffff;
}
.timeline-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.timeline-title {
  font-size: 16px;
  font-weight: bold;
  color: #003366;
  margin: 0;
}

.timeline-company {
  font-size: 14px;
  color: #333;
  margin-top: 4px;
}

.timeline-date {
  font-size: 14px;
  color: #888;
  white-space: nowrap;
}

.timeline-description {
  font-size: 14px;
  color: #333;
  margin-top: 10px;
  line-height: 1.6;
}
@media (max-width: 768px) {
  .timeline-left {
    display: none;
  }

  .timeline-container::before {
    left: 40px;
  }

  .timeline-card::before {
    left: 10px; /* sticks out to the left */
  }

  .timeline-item {
    flex: 1;
    padding-right: 30px;
  }
  .timeline-right {
    flex: 1;
    padding-right: 30px;
  }
  .timeline-center {
    width: 20px;
    left: -46px;
    position: relative;
  }
}
